import _ from 'lodash';
import React, { Component } from 'react';
import RouteDetail from './route-detail';
import { Label, Table } from 'react-bootstrap';

export default class SearchResult extends Component {
  constructor(props) {
    super(props);

    this.findPortInfo = this.findPortInfo.bind(this);
  }

  findPortInfo(queryText) {
    const { allPorts } = this.props.search;
    const portInfo = _.find(allPorts, { 'id': queryText });
    return <span>{ portInfo.name }({portInfo.locationCode}) / { portInfo.type }</span>;
  }

  render() {
    const { transports, allPorts, routes } = this.props.search;

    return (
      <div className="result-container">
        <h4 className="count"> : <em>{ routes.resultCount }</em></h4>
        {
          (() => {
            if (_.isEqual(routes.resultCount, 0)) {
                return <p className="no-result">  .</p>;
            }
            return (
              <div className="search-result">
                <div className="search-th">
                  <span className="td sport">Orig. { this.findPortInfo(_.nth(routes[0], 2)) }</span>
                  <span className="td dport">Dest. { this.findPortInfo(_.last(route[0])) }</span>
                </div>
                <div className="route-box">
                  <Table>
                    <tbody>
                      <tr>
                        <td>
                          <Label>0 days</Label>
                        </td>
                        <td>
                          <Label>5 days</Label>
                        </td>
                        <td>
                          <Label>10 days</Label>
                        </td>
                        <td>
                          <Label>15 days</Label>
                        </td>
                        <td>
                          <Label>20 days</Label>
                        </td>
                        <td>
                          <Label>25 days</Label>
                        </td>
                        <td>
                          <Label>30 days</Label>
                        </td>
                        <td>
                          <Label>35 days</Label>
                        </td>
                        <td>
                          <Label>40 days</Label>
                        </td>
                        <td>
                          <Label>45 days</Label>
                        </td>
                      </tr>
                    </tbody>
                  </Table>
                </div>
                <ul className="list">
                {
                  _.map(routes.routes, (route, index) => (
                    <RouteDetail key={index} route={route} allPorts={allPorts} transports={transports} />
                  ))
                }
                </ul>
              </div>
            );
          })()
        }

      </div>
    );
  }
}



